.caixa-ramen {
  width: 300px;
  height: 400px;
  cursor: pointer;
  margin: 20px;
  padding: 10px;
  transition: all .8s .4s;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}

.ramen-frente {
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: center;
  padding: 5px;
  display: flex;
}

.ramen-atras {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.caixa-ramen__imagem-ramen {
  max-width: 80%;
  flex-shrink: 2;
}

.caixa-ramen__nome-ramen {
  font-family: var(--fonte-fantasia);
  text-align: center;
  margin: 5px 0;
  font-size: 2rem;
}

.caixa-ramen__atributos-ramen {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.atributos-ramen__item {
  background-color: var(--branco-escuro);
  box-sizing: border-box;
  font-size: .8rem;
  font-family: var(--fonte-secundaria);
  margin: 5px;
  padding: 10px;
  transition: all .5s .4s;
}

